@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
  src: url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  src: url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2");
  src: url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
img {
  max-width: 100%;
  object-fit: contain;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
  overflow-x: hidden;
}
.page__body {
  font-family: "Lato", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: #444444;
  margin: 0;
  position: relative;
}
.wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  min-height: 100%;
  width: 320px;
}
@media (min-width: 769px) {
  .wrapper {
    width: 769px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    width: 1440px;
  }
}
.main-wrapper {
  flex-grow: 1;
}
.location__iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.main-header {
  display: flex;
  flex-direction: column;
}
.main-header__logo-wrapper {
  position: relative;
  padding: 0 20px;
  padding-top: 10px;
  padding-bottom: 17px;
  border-bottom: 1px #e6e6e6 solid;
}
.main-header__logo {
  width: 191px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 769px) {
  .main-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-top: 18px;
    width: 708px;
    margin: 0 auto;
  }
  .main-header__logo-wrapper {
    border-bottom: none;
    padding: 0;
    position: static;
  }
  .main-header__logo {
    width: 174px;
    position: absolute;
    top: 0;
  }
  .main-header__logo-image {
    display: none;
  }
}
@media (min-width: 1440px) {
  .main-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-top: 69px;
    width: 1220px;
    margin: 0 auto;
    position: relative;
  }
  .main-header--index::before {
    content: "";
    background-image: url("../img/index-background-desktop.jpg");
    background-color: rgba(104, 183, 56, 0.85);
    background-size: 720px 696px, 720px 696px;
    background-position: center, center;
    background-repeat: no-repeat;
    width: 720px;
    height: 696px;
    position: absolute;
    top: 0;
    right: -110px;
    z-index: -2;
  }
  .main-header__logo-wrapper {
    border-bottom: none;
  }
  .main-header__logo {
    width: 202px;
    position: static;
  }
  .main-header__logo-image {
    display: none;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .main-header--index::before {
    background-image: url("../img/index-background-desktop@2x.jpg");
  }
}
.main-nav {
  position: relative;
}
.main-nav--opened .main-nav__list {
  display: block;
}
.main-nav--closed .main-nav__list {
  display: none;
}
.main-nav--nojs .main-nav__list {
  width: 320px;
  position: static;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
  width: 320px;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
}
.main-nav__item {
  border-bottom: 2px #e6e6e6 solid;
}
.main-nav__item a[href]:hover {
  opacity: 0.6;
}
.main-nav__item a[href]:active {
  opacity: 0.3;
}
.main-nav__link {
  padding-top: 22px;
  padding-bottom: 22px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #ffffff;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  display: block;
  font-family: "Oswald", sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  color: #000000;
}
@media (min-width: 769px) {
  .main-nav__list {
    position: relative;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav--closed .main-nav__list,
  .main-nav--opened .main-nav__list {
    width: 440px;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
  .main-nav__item {
    border-bottom: none;
    margin-right: 35px;
  }
  .main-nav__item:last-child {
    margin-right: 0;
  }
  .main-nav__item--current {
    position: relative;
  }
  .main-nav__item--current::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background: #68b738;
  }
  .main-nav__link {
    padding: 0;
    padding-bottom: 10px;
    background-color: #ffffff;
    font-family: "Oswald", sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    color: #000000;
  }
}
@media (min-width: 1440px) {
  .main-nav--closed .main-nav__list,
  .main-nav--opened .main-nav__list {
    width: 478px;
  }
  .main-nav__item {
    margin-right: 34px;
  }
  .main-nav__item:last-child {
    margin-right: 0;
  }
  .main-nav--index .main-nav__item--current::after {
    background: #ffffff;
  }
  .main-nav__link {
    font-family: "Oswald", sans-serif;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    color: #000000;
  }
  .main-nav--index .main-nav__link {
    font-family: "Oswald", sans-serif;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;
    color: #ffffff;
    background-color: inherit;
  }
}
.menu-button {
  background-color: inherit;
  border: none;
  cursor: pointer;
}
.menu-button--close {
  background-image: url("../img/stack.svg#menu-mobile-close");
  background-size: 20px 20px;
  background-repeat: no-repeat;
  background-position: center;
  width: 30px;
  height: 30px;
  padding: 5px;
  position: absolute;
  top: -51px;
  right: 18px;
}
.menu-button--open {
  background-image: url("../img/stack.svg#menu-mobile-open");
  background-size: 24px 18px;
  background-repeat: no-repeat;
  background-position: center;
  width: 30px;
  height: 30px;
  padding: 3px;
  position: absolute;
  top: -50px;
  right: 18px;
}
.main-index__container-contact-location {
  position: relative;
}
.main-index__location {
