.diet__container {
  width: 100%;
  padding-bottom: 23px;
  margin-bottom: 15px;
  border-bottom: 1px #d9d9d9 solid;
}
.diet {
  background-color: #f2f2f2;
  padding-left: 20px;
  padding-right: 19px;
  padding-top: 30px;
  padding-bottom: 18px;
  position: relative;
}
.diet::before {
  position: absolute;
  content: "";
  background-color: #68b738;
  background-size: 50px 50px;
  width: 50px;
  height: 50px;
  top: 24px;
  left: 22px;
  border-radius: 50%;
}
.diet--slim::after {
  content: "";
  background-image: url("../img/stack.svg#cat-slim");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 50px 53px;
  width: 50px;
  height: 53px;
  position: absolute;
  top: 20px;
  left: 22px;
}
.diet--pro::after {
  content: "";
  background-image: url("../img/stack.svg#cat-pro");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 67px 50px;
  width: 67px;
  height: 50px;
  position: absolute;
  top: 24px;
  left: 13px;
}
